"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { ArrowRight } from "lucide-react"

interface BirthdayCakeProps {
  onContinue: () => void
  onBlowCandles: () => void
}

export function BirthdayCake({ onContinue, onBlowCandles }: BirthdayCakeProps) {
  const [candlesLit, setCandlesLit] = useState([true, true, true, true, true])
  const [allBlown, setAllBlown] = useState(false)
  const [showMessage, setShowMessage] = useState(false)

  const blowCandle = (index: number) => {
    if (!candlesLit[index]) return
    
    const newCandles = [...candlesLit]
    newCandles[index] = false
    setCandlesLit(newCandles)
    
    // Check if all candles are blown
    if (newCandles.every(c => !c)) {
      setAllBlown(true)
      onBlowCandles()
      setTimeout(() => setShowMessage(true), 500)
    }
  }

  const blowAllCandles = () => {
    setCandlesLit([false, false, false, false, false])
    setAllBlown(true)
    onBlowCandles()
    setTimeout(() => setShowMessage(true), 500)
  }

  return (
    <section className="min-h-screen flex flex-col items-center justify-center p-6">
      <div className="text-center max-w-lg mx-auto">
        <p className="text-sm uppercase tracking-widest text-muted-foreground mb-4">Make A Wish</p>
        <h2 className="text-3xl md:text-4xl font-medium text-foreground mb-2 tracking-tight">
          {allBlown ? "üéâ Happy Birthday! üéâ" : "Blow The Candles"}
        </h2>
        <p className="text-muted-foreground mb-8">
          {allBlown ? "Your wish is on its way!" : "Click each candle to blow it out"}
        </p>

        {/* Cake */}
        <div className="relative mx-auto mb-8">
          {/* Candles */}
          <div className="flex justify-center gap-6 mb-4">
            {candlesLit.map((isLit, i) => (
              <button
                key={i}
                onClick={() => blowCandle(i)}
                className="relative group cursor-pointer transition-transform hover:scale-110"
                disabled={!isLit}
              >
                {/* Flame */}
                {isLit && (
                  <div className="absolute -top-6 left-1/2 -translate-x-1/2 w-4 h-6">
                    <div className="absolute inset-0 bg-gradient-to-t from-orange-500 via-yellow-400 to-yellow-200 rounded-full animate-flicker" 
                         style={{ clipPath: "ellipse(50% 70% at 50% 70%)" }} />
                    <div className="absolute inset-1 bg-gradient-to-t from-orange-300 to-yellow-100 rounded-full opacity-80 animate-flicker-inner" 
                         style={{ clipPath: "ellipse(40% 60% at 50% 60%)" }} />
                  </div>
                )}
                {/* Candle stick */}
                <div className={`w-3 h-12 rounded-t-sm transition-all duration-300 ${
                  isLit 
                    ? "bg-gradient-to-b from-pink-300 to-pink-400" 
                    : "bg-gradient-to-b from-gray-300 to-gray-400"
                }`}>
                  {/* Smoke when blown */}
                  {!isLit && (
                    <div className="absolute -top-4 left-1/2 -translate-x-1/2 w-2 h-4 opacity-40">
                      <div className="w-full h-full bg-gray-400 rounded-full animate-smoke" />
                    </div>
                  )}
                </div>
              </button>
            ))}
          </div>

          {/* Cake layers */}
          <div className="relative">
            {/* Top layer */}
            <div className="mx-auto w-48 h-8 bg-gradient-to-b from-pink-200 to-pink-300 rounded-t-lg border-b-4 border-pink-400">
              <div className="absolute inset-x-4 top-2 h-1 bg-white/30 rounded-full" />
            </div>
            {/* Frosting drips */}
            <div className="mx-auto w-52 h-4 bg-gradient-to-b from-white to-pink-100 -mt-1 relative">
              <div className="absolute -bottom-2 left-4 w-3 h-4 bg-white rounded-b-full" />
              <div className="absolute -bottom-3 left-12 w-4 h-5 bg-white rounded-b-full" />
              <div className="absolute -bottom-2 right-8 w-3 h-4 bg-white rounded-b-full" />
              <div className="absolute -bottom-4 right-16 w-4 h-6 bg-white rounded-b-full" />
            </div>
            {/* Middle layer */}
            <div className="mx-auto w-52 h-10 bg-gradient-to-b from-pink-300 to-pink-400 border-b-4 border-pink-500" />
            {/* Bottom layer */}
            <div className="mx-auto w-56 h-12 bg-gradient-to-b from-pink-400 to-pink-500 rounded-b-lg">
              <div className="absolute bottom-2 inset-x-8 h-2 flex justify-around">
                {[...Array(5)].map((_, i) => (
                  <div key={i} className="w-2 h-2 bg-red-400 rounded-full" />
                ))}
              </div>
            </div>
            {/* Plate */}
            <div className="mx-auto w-64 h-3 bg-gradient-to-b from-gray-200 to-gray-300 rounded-full mt-1 shadow-md" />
          </div>
        </div>

        {/* Blow all button */}
        {!allBlown && candlesLit.some(c => c) && (
          <Button 
            onClick={blowAllCandles} 
            variant="outline" 
            size="lg" 
            className="mb-4 bg-transparent"
          >
            ‚ú® Blow All Candles
          </Button>
        )}

        {/* Continue after blowing */}
        {showMessage && (
          <div className="animate-fade-in space-y-4">
            <p className="text-lg text-foreground font-medium">
              Now let's see what surprises await you!
            </p>
            <Button onClick={onContinue} size="lg" className="px-8">
              Continue
              <ArrowRight className="w-4 h-4 ml-2" />
            </Button>
          </div>
        )}
      </div>
    </section>
  )
}
