"use client"

import { useState } from "react"
import { ArrowRight } from "lucide-react"
import { Button } from "@/components/ui/button"

interface PhotoCollageProps {
  onContinue: () => void
}

export function PhotoCollage({ onContinue }: PhotoCollageProps) {
  const [revealedPhotos, setRevealedPhotos] = useState<number[]>([])

  // To use your own photos:
  // 1. Upload foto ke folder "public" di project
  // 2. Ganti "src" dengan path foto, contoh: "/foto1.jpg"
  // 3. Ganti "caption" dengan deskripsi foto
  const photos = [
    { src: "/romantic-couple-photo-memory.jpg", caption: "Our first date" },
    { src: "/sunset-together-romantic.jpg", caption: "That sunset together" },
    { src: "/smiling-girl-portrait.jpg", caption: "Your beautiful smile" },
    { src: "/couple-adventure-travel.jpg", caption: "Adventures with you" },
    { src: "/couple-laughing-happy.jpg", caption: "Laughing together" },
    { src: "/romantic-couple-portrait.png", caption: "My favorite person" },
  ]

  const handleReveal = (index: number) => {
    if (!revealedPhotos.includes(index)) {
      setRevealedPhotos([...revealedPhotos, index])
    }
  }

  return (
    <section className="min-h-screen flex flex-col items-center justify-center p-6">
      <div className="max-w-3xl mx-auto w-full">
        <div className="text-center mb-8">
          <p className="text-sm uppercase tracking-widest text-muted-foreground mb-3">Looking Back</p>
          <h2 className="text-3xl md:text-4xl font-medium mb-2 text-foreground tracking-tight">Our Memories</h2>
          <p className="text-muted-foreground">Click each envelope to reveal our moments</p>
        </div>

        {/* Photo Grid with Envelope Reveal - Improved paper flip animation for smoother reveal */}
        <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mb-8">
          {photos.map((photo, i) => {
            const revealed = revealedPhotos.includes(i)
            return (
              <div key={i} className="relative aspect-square cursor-pointer perspective" onClick={() => handleReveal(i)}>
                {/* Envelope (shown when not revealed) */}
                <div
                  className={`absolute inset-0 transition-all duration-700 ease-in-out delayed ${
                    revealed ? "opacity-0 scale-95 pointer-events-none" : "opacity-100 scale-100"
                  }`}
                  style={{
                    transformStyle: revealed ? "preserve-3d" : undefined,
                    ['--delay' as any]: `${i * 60}ms`,
                  }}
                >
                  <div className="w-full h-full bg-card border border-border rounded-lg flex flex-col items-center justify-center hover:bg-accent/50 transition-colors duration-200">
                    {/* Envelope Body with flap */}
                    <div className={`relative w-16 h-12 md:w-20 md:h-14 envelope ${revealed ? 'open' : ''}`}>
                      <div className="absolute inset-0 bg-primary/10 border border-primary/20 rounded-sm" />

                      {/* Flap */}
                      <div
                        className="flap absolute -top-1 left-0 right-0 h-6 md:h-7 border-l border-r border-t border-primary/20 bg-primary/5"
                        style={{
                          clipPath: "polygon(0 100%, 50% 30%, 100% 100%)",
                        }}
                      />

                      <div className="absolute top-2 left-1/2 -translate-x-1/2 w-3 h-3 md:w-4 md:h-4 bg-primary/30 rounded-full" />
                    </div>
                    <p className="text-xs text-muted-foreground mt-3">Click to open</p>
                  </div>
                </div>

                {/* Photo (revealed) - pop out like paper (polaroid/photo-box style) */}
                <div
                  className={`absolute inset-0 ${revealed ? 'opacity-100 scale-100 paper-pop paper-float' : 'opacity-0 scale-90'} transition-all duration-700 ease-out delayed`}
                  style={{
                    transformStyle: revealed ? "preserve-3d" : undefined,
                    ['--delay' as any]: `${i * 80}ms`,
                  }}
                >
                  <div className="relative w-full h-full flex items-end justify-center">
                    <figure
                      className="photo-box"
                      style={{
                        ['--rot' as any]: `${(i % 5 - 2) * 4}deg`,
                        zIndex: revealed ? 10 + i : undefined,
                      }}
                    >
                      <div className="photo-frame">
                        <img
                          src={photo.src || "/placeholder.svg"}
                          alt={photo.caption}
                          className="photo-image"
                        />
                      </div>
                      <figcaption className="polaroid-caption">{photo.caption}</figcaption>
                    </figure>
                  </div>
                </div>
              </div>
            )
          })}
        </div>

        <p className="text-center text-xs text-muted-foreground mb-6">
          {revealedPhotos.length} of {photos.length} memories revealed
        </p>

        <div className="text-center">
          <Button onClick={onContinue} variant="outline" size="lg" className="px-8 bg-transparent">
            One More Surprise
            <ArrowRight className="w-4 h-4 ml-2" />
          </Button>
        </div>
      </div>
    </section>
  )
}
