"use client"

import { useState } from "react"
import { Send, Heart, Check } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"

interface MessageFormProps {
  onSent?: () => void
}

export function MessageForm({ onSent }: MessageFormProps) {
  const [message, setMessage] = useState("")
  const [name, setName] = useState("")
  const [isSending, setIsSending] = useState(false)
  const [isSent, setIsSent] = useState(false)

  const recipientEmail = process.env.NEXT_PUBLIC_RECIPIENT_EMAIL || "your-email@example.com"

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!message.trim()) return

    setIsSending(true)

    // Create mailto link with the message
    const subject = encodeURIComponent(`üíå Birthday Reply from ${name || "Someone Special"}`)
    const body = encodeURIComponent(`${message}\n\n---\nSent with love from the Birthday Website üíù`)
    
    // Open email client
    window.location.href = `mailto:${recipientEmail}?subject=${subject}&body=${body}`

    // Show success state
    setTimeout(() => {
      setIsSending(false)
      setIsSent(true)
      onSent?.()
    }, 1000)
  }

  if (isSent) {
    return (
      <Card className="bg-card border border-border p-6 md:p-8 text-center">
        <div className="w-16 h-16 bg-green-100 dark:bg-green-900/30 rounded-full flex items-center justify-center mx-auto mb-4">
          <Check className="w-8 h-8 text-green-600 dark:text-green-400" />
        </div>
        <h3 className="text-xl font-medium text-foreground mb-2">Message Ready!</h3>
        <p className="text-muted-foreground text-sm">
          Your email client should have opened. If not, you can copy your message and send it manually.
        </p>
        <div className="mt-4 flex items-center justify-center gap-1 text-primary">
          <Heart className="w-4 h-4 fill-current" />
          <span className="text-sm">Thank you for your sweet words!</span>
          <Heart className="w-4 h-4 fill-current" />
        </div>
      </Card>
    )
  }

  return (
    <Card className="bg-card border border-border p-6 md:p-8">
      <div className="text-center mb-6">
        <Heart className="w-8 h-8 text-primary mx-auto mb-3" />
        <h3 className="text-xl font-medium text-foreground mb-2">Write Back To Me</h3>
        <p className="text-sm text-muted-foreground">
          I'd love to hear from you too! Write me something special.
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="name" className="block text-sm font-medium text-foreground mb-2">
            Your Name (optional)
          </label>
          <input
            type="text"
            id="name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="Your lovely name..."
            className="w-full px-4 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary/50 transition-all"
          />
        </div>

        <div>
          <label htmlFor="message" className="block text-sm font-medium text-foreground mb-2">
            Your Message <span className="text-primary">*</span>
          </label>
          <textarea
            id="message"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            placeholder="Write something sweet..."
            rows={5}
            required
            className="w-full px-4 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary/50 transition-all resize-none"
          />
        </div>

        <Button 
          type="submit" 
          size="lg" 
          className="w-full"
          disabled={!message.trim() || isSending}
        >
          {isSending ? (
            <>
              <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin mr-2" />
              Sending...
            </>
          ) : (
            <>
              Send My Love
              <Send className="w-4 h-4 ml-2" />
            </>
          )}
        </Button>
      </form>
    </Card>
  )
}
